import { clsx, type ClassValue } from "clsx"
import { twMerge } from "tailwind-merge"

export function cn(...inputs: ClassValue[]) {
  return twMerge(clsx(inputs))
}

// Data Types
export interface Asset {
  id: string
  name: string
  category: string
  quantity: number
  description?: string
  location?: string
  purchaseDate?: string
  condition: 'Baik' | 'Rusak Ringan' | 'Rusak Berat' | 'Tidak Dapat Digunakan'
  createdAt: string
  updatedAt: string
}

export interface Category {
  id: string
  name: string
  description?: string
  createdAt: string
  updatedAt: string
}

export interface User {
  id: string
  name: string
  email: string
  role: 'Admin' | 'Staff' | 'Viewer'
  createdAt: string
  updatedAt: string
}

// Storage Keys
const STORAGE_KEYS = {
  ASSETS: 'sipba_assets',
  CATEGORIES: 'sipba_categories',
  USERS: 'sipba_users',
} as const

// Generic Storage Functions
function getFromStorage<T>(key: string, defaultValue: T[] = []): T[] {
  try {
    if (typeof window === 'undefined') return defaultValue
    const data = localStorage.getItem(key)
    return data ? JSON.parse(data) : defaultValue
  } catch (error) {
    console.error(`Error reading ${key} from localStorage:`, error)
    return defaultValue
  }
}

function saveToStorage<T>(key: string, data: T[]): void {
  try {
    if (typeof window === 'undefined') return
    localStorage.setItem(key, JSON.stringify(data))
  } catch (error) {
    console.error(`Error saving ${key} to localStorage:`, error)
  }
}

// Asset Management Functions
export function getAssets(): Asset[] {
  return getFromStorage<Asset>(STORAGE_KEYS.ASSETS)
}

export function saveAssets(assets: Asset[]): void {
  saveToStorage(STORAGE_KEYS.ASSETS, assets)
}

export function addAsset(asset: Omit<Asset, 'id' | 'createdAt' | 'updatedAt'>): Asset {
  const assets = getAssets()
  const newAsset: Asset = {
    ...asset,
    id: generateId(),
    createdAt: new Date().toISOString(),
    updatedAt: new Date().toISOString(),
  }
  assets.push(newAsset)
  saveAssets(assets)
  return newAsset
}

export function updateAsset(id: string, updates: Partial<Omit<Asset, 'id' | 'createdAt'>>): Asset | null {
  const assets = getAssets()
  const index = assets.findIndex(asset => asset.id === id)
  
  if (index === -1) return null
  
  assets[index] = {
    ...assets[index],
    ...updates,
    updatedAt: new Date().toISOString(),
  }
  
  saveAssets(assets)
  return assets[index]
}

export function deleteAsset(id: string): boolean {
  const assets = getAssets()
  const filteredAssets = assets.filter(asset => asset.id !== id)
  
  if (filteredAssets.length === assets.length) return false
  
  saveAssets(filteredAssets)
  return true
}

export function getAssetById(id: string): Asset | null {
  const assets = getAssets()
  return assets.find(asset => asset.id === id) || null
}

