// Utility Functions
export function generateId(): string {
  return Date.now().toString(36) + Math.random().toString(36).substr(2)
}

export function formatDate(dateString: string): string {
  try {
    return new Date(dateString).toLocaleDateString('id-ID', {
      year: 'numeric',
      month: 'long',
      day: 'numeric',
    })
  } catch (error) {
    return dateString
  }
}

export function exportToCSV(data: any[], filename: string): void {
  try {
    if (data.length === 0) return
    
    const headers = Object.keys(data[0])
    const csvContent = [
      headers.join(','),
      ...data.map(row => 
        headers.map(header => {
          const value = row[header]
          return typeof value === 'string' && value.includes(',') 
            ? `"${value}"` 
            : value
        }).join(',')
      )
    ].join('\n')
    
    const blob = new Blob([csvContent], { type: 'text/csv;charset=utf-8;' })
    const link = document.createElement('a')
    
    if (link.download !== undefined) {
      const url = URL.createObjectURL(blob)
      link.setAttribute('href', url)
      link.setAttribute('download', filename)
      link.style.visibility = 'hidden'
      document.body.appendChild(link)
      link.click()
      document.body.removeChild(link)
    }
  } catch (error) {
    console.error('Error exporting to CSV:', error)
  }
}

// Initialize default data
export function initializeDefaultData(): void {
  try {
    // Initialize categories if empty
    const categories = getCategories()
    if (categories.length === 0) {
      const defaultCategories = [
        { name: 'Peralatan Medis', description: 'Alat-alat medis dan kesehatan' },
        { name: 'Furniture', description: 'Meja, kursi, lemari, dan furniture lainnya' },
        { name: 'Elektronik', description: 'Komputer, printer, dan peralatan elektronik' },
        { name: 'Kendaraan', description: 'Ambulans dan kendaraan operasional' },
        { name: 'Obat-obatan', description: 'Persediaan obat dan farmasi' },
      ]
      
      defaultCategories.forEach(category => addCategory(category))
    }
    
    // Initialize users if empty
    const users = getUsers()
    if (users.length === 0) {
      const defaultUsers = [
        { name: 'Administrator', email: 'admin@puskesmaskresek.id', role: 'Admin' as const },
        { name: 'Staff Inventaris', email: 'staff@puskesmaskresek.id', role: 'Staff' as const },
      ]
      
      defaultUsers.forEach(user => addUser(user))
    }
  } catch (error) {
    console.error('Error initializing default data:', error)
  }
}
