'use client'

import { useState, useEffect } from 'react'
import { Button } from '@/components/ui/button'
import { Input } from '@/components/ui/input'
import { Label } from '@/components/ui/label'
import { Textarea } from '@/components/ui/textarea'
import {
  Dialog,
  DialogContent,
  DialogHeader,
  DialogTitle,
} from '@/components/ui/dialog'
import { addAsset, updateAsset, type Asset } from '@/lib/utils'

interface AssetModalProps {
  isOpen: boolean
  onClose: (shouldRefresh?: boolean) => void
  asset?: Asset | null
  categories: string[]
  onSuccess: (message: string) => void
  onError: (message: string) => void
}

interface FormData {
  name: string
  category: string
  quantity: number
  description: string
  location: string
  purchaseDate: string
  condition: Asset['condition']
}

export function AssetModal({ 
  isOpen, 
  onClose, 
  asset, 
  categories, 
  onSuccess, 
  onError 
}: AssetModalProps) {
  const [formData, setFormData] = useState<FormData>({
    name: '',
    category: '',
    quantity: 1,
    description: '',
    location: '',
    purchaseDate: '',
    condition: 'Baik'
  })
  const [errors, setErrors] = useState<Partial<FormData>>({})
  const [isSubmitting, setIsSubmitting] = useState(false)

  const conditionOptions: Asset['condition'][] = [
    'Baik',
    'Rusak Ringan', 
    'Rusak Berat',
    'Tidak Dapat Digunakan'
  ]

  useEffect(() => {
    if (asset) {
      setFormData({
        name: asset.name,
        category: asset.category,
        quantity: asset.quantity,
        description: asset.description || '',
        location: asset.location || '',
        purchaseDate: asset.purchaseDate || '',
        condition: asset.condition
      })
    } else {
      setFormData({
        name: '',
        category: categories[0] || '',
        quantity: 1,
        description: '',
        location: '',
        purchaseDate: '',
        condition: 'Baik'
      })
    }
    setErrors({})
  }, [asset, categories, isOpen])

  const validateForm = (): boolean => {
    const newErrors: Partial<FormData> = {}

    if (!formData.name.trim()) {
      newErrors.name = 'Nama aset harus diisi'
    }

    if (!formData.category) {
      newErrors.category = 'Kategori harus dipilih'
    }

    if (formData.quantity < 1) {
      newErrors.quantity = 'Jumlah harus lebih dari 0'
    }

    setErrors(newErrors)
    return Object.keys(newErrors).length === 0
  }

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault()
    
    if (!validateForm()) return

    setIsSubmitting(true)

    try {
      const assetData = {
        name: formData.name.trim(),
        category: formData.category,
        quantity: formData.quantity,
        description: formData.description.trim() || undefined,
        location: formData.location.trim() || undefined,
        purchaseDate: formData.purchaseDate || undefined,
        condition: formData.condition
      }

      if (asset) {
        // Update existing asset
        const updatedAsset = updateAsset(asset.id, assetData)
        if (updatedAsset) {
          onSuccess('Aset berhasil diperbarui')
          onClose(true)
        } else {
          onError('Gagal memperbarui aset')
        }
      } else {
        // Add new asset
        const newAsset = addAsset(assetData)
        if (newAsset) {
          onSuccess('Aset berhasil ditambahkan')
          onClose(true)
        } else {
          onError('Gagal menambahkan aset')
        }
      }
    } catch (error) {
      console.error('Error saving asset:', error)
      onError('Terjadi kesalahan saat menyimpan aset')
    } finally {
      setIsSubmitting(false)
    }
  }

  const updateFormField = (field: keyof FormData, value: string | number) => {
    setFormData(prev => ({ ...prev, [field]: value }))
    if (errors[field]) {
      setErrors(prev => ({ ...prev, [field]: undefined }))
    }
  }

  return (
    <Dialog open={isOpen} onOpenChange={() => onClose()}>
      <DialogContent className="sm:max-w-[500px] max-h-[90vh] overflow-y-auto">
        <DialogHeader>
          <DialogTitle>
            {asset ? 'Edit Aset' : 'Tambah Aset Baru'}
          </DialogTitle>
        </DialogHeader>

        <form onSubmit={handleSubmit} className="space-y-4">
          {/* Name */}
          <div className="space-y-2">
            <Label htmlFor="name">Nama Aset *</Label>
            <Input
              id="name"
              value={formData.name}
              onChange={(e) => updateFormField('name', e.target.value)}
              placeholder="Masukkan nama aset"
              className={errors.name ? 'border-red-500' : ''}
            />
            {errors.name && (
              <p className="text-sm text-red-600">{errors.name}</p>
            )}
          </div>

          {/* Category */}
          <div className="space-y-2">
            <Label htmlFor="category">Kategori *</Label>
            <select
              id="category"
              value={formData.category}
              onChange={(e) => updateFormField('category', e.target.value)}
              className={`w-full px-3 py-2 border rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500 ${
                errors.category ? 'border-red-500' : 'border-gray-300'
              }`}
            >
              <option value="">Pilih kategori</option>
              {categories.map(category => (
                <option key={category} value={category}>
                  {category}
                </option>
              ))}
            </select>
            {errors.category && (
              <p className="text-sm text-red-600">{errors.category}</p>
            )}
          </div>

          {/* Quantity */}
          <div className="space-y-2">
            <Label htmlFor="quantity">Jumlah *</Label>
            <Input
              id="quantity"
              type="number"
              min="1"
              value={formData.quantity}
              onChange={(e) => updateFormField('quantity', parseInt(e.target.value) || 1)}
              className={errors.quantity ? 'border-red-500' : ''}
            />
            {errors.quantity && (
              <p className="text-sm text-red-600">{errors.quantity}</p>
            )}
          </div>

          {/* Condition */}
          <div className="space-y-2">
            <Label htmlFor="condition">Kondisi</Label>
            <select
              id="condition"
              value={formData.condition}
              onChange={(e) => updateFormField('condition', e.target.value as Asset['condition'])}
              className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500"
            >
              {conditionOptions.map(condition => (
                <option key={condition} value={condition}>
                  {condition}
                </option>
              ))}
            </select>
          </div>

          {/* Location */}
          <div className="space-y-2">
            <Label htmlFor="location">Lokasi</Label>
            <Input
              id="location"
              value={formData.location}
              onChange={(e) => updateFormField('location', e.target.value)}
              placeholder="Contoh: Ruang Perawatan, Gudang, dll."
            />
          </div>

          {/* Purchase Date */}
          <div className="space-y-2">
            <Label htmlFor="purchaseDate">Tanggal Pembelian</Label>
            <Input
              id="purchaseDate"
              type="date"
              value={formData.purchaseDate}
              onChange={(e) => updateFormField('purchaseDate', e.target.value)}
            />
          </div>

          {/* Description */}
          <div className="space-y-2">
            <Label htmlFor="description">Deskripsi</Label>
            <Textarea
              id="description"
              value={formData.description}
              onChange={(e) => updateFormField('description', e.target.value)}
              placeholder="Deskripsi tambahan tentang aset ini..."
              rows={3}
            />
          </div>

          {/* Actions */}
          <div className="flex justify-end gap-2 pt-4">
            <Button
              type="button"
              variant="outline"
              onClick={() => onClose()}
              disabled={isSubmitting}
            >
              Batal
            </Button>
            <Button
              type="submit"
              disabled={isSubmitting}
            >
              {isSubmitting 
                ? 'Menyimpan...' 
                : asset 
                  ? 'Perbarui Aset' 
                  : 'Tambah Aset'
              }
            </Button>
          </div>
        </form>
      </DialogContent>
    </Dialog>
  )
}
